import React, { useEffect, useState } from "react";
import ChangePass from "./child/ChangePass";
import ModalUID from "../../components/ModalUID";
export default function ProfilePage() {
  const [state, setState] = useState({
    modal : {
      componentAction: null,
      title : "",
      method : null
    }
  });

  var { modal } = state;
  return (
    <div className="container-fluid">
  
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">Hồ sơ</h4>
        <button
          onClick={() =>
            setState({ ...state, modal : { componentAction: <ChangePass></ChangePass> , title : "Thay đổi mật khẩu"  , method : "EDIT"}})
          }
          data-toggle="modal"
          data-target="#modalUID"
          className="btn btn-info btn-icon-split btn-sm "
          style={{ height: "fit-content", width: "fit-content" }}
        >
          <span className="icon text-white-50">
            <i className="fas fa-plus" />
          </span>
          <span className="text " style={{ color: "white" }}>
            Thay đổi mật khẩu
          </span>
        </button>
      </div>
      <div className="card shadow mb-4">
        <div className="card-header py-3">
          <h6 className="m-0 title_content font-weight-bold text-primary">
            Thông tin cá nhân
          </h6>
        </div>
        <div className="card-body">
          <form role="form">
            <div className="box-body">
              <div className="box-body">
                {/* <div className="form-group">
                  <label>Ảnh: &nbsp; </label>
                  <img src="https://isempai.net/images/no_img.png" width={150} height={150} />
                </div>
                <div className="form-group">
                  <div className="kv-avatar">
                    <div>
                      <button
                        type="button"
                        className="btn btn-primary btn-sm"
                        data-toggle="modal"
                        data-target="#uploadModalProfile"
                      >
                        <i className="fa fa-plus" /> Upload ảnh
                      </button>
                    </div>
                  </div>
                </div> */}
                <div className="form-group">
                  <label htmlFor="name">Họ và tên</label>
                  <input
                    type="text"
                    className="form-control"
                    id="txtName"
                    name="txtName"
                    autoComplete="off"
                  />
                </div>
                <div className="form-group gender">
                  <label htmlFor="gender">Giới tính</label>
                  <div className="radio">
                    <label>
                      <input
                        type="radio"
                        name="txtSex"
                        className="male"
                        id="male"
                      />
                      Nam
                    </label>
                    <label>
                      <input
                        type="radio"
                        name="txtSex"
                        className="male"
                        id="female"
                      />
                      Nữ
                    </label>
                    <label>
                      <input
                        type="radio"
                        name="txtSex"
                        className="male"
                        id="another"
                      />
                      Khác
                    </label>
                  </div>
                </div>
                <div className="form-group">
                  <label htmlFor="fname">Ngày sinh</label>
                  <input
                    disabled
                    type="number"
                    className="form-control"
                    id="txtPhone"
                    name="txtPhone"
                    autoComplete="off"
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="lname">Số điện thoại</label>
                  <input
                    disabled
                    type="number"
                    className="form-control"
                    id="txtPhone"
                    name="txtPhone"
                    autoComplete="off"
                    defaultValue={"0389633548"}
                  />
                </div>
          
                <div className="form-group">
                  <label htmlFor="phone">Email</label>
                  <input
                    disabled
                    type="text"
                    className="form-control"
                    id="txtEmail"
                    name="txtEmail"
                    autoComplete="off"
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="phone">Ngày tạo</label>
                  <input
                    disabled
                    type="text"
                    className="form-control"
                    id="txtCreateStore"
                    name="txtCreateStore"
                    autoComplete="off"
                    defaultValue="05-07-2022"
                  />
                </div>
              </div>
            </div>
            <div className="box-footer">
              <button type="submit" className="btn btn-info  btn-sm">
                <i className="fas fa-save" />
                Lưu
              </button>
            </div>
          </form>
     
        </div>
      </div>
      <ModalUID method = {modal.method} title = {modal.title}> {modal.componentAction} </ModalUID>

    </div>
  );
}
